@import "main";
@import url('https://fonts.googleapis.com/css2?family=Dokdo&display=swap');

.header {
	position : sticky;
	top : -1vw;
	height : 5vw;
	width : 100vw;
	border-bottom : 1px solid black;
}

.blog_image {
	width : 10vw;
	height : 5vw;
	float : left;
}

.blog_image img{
	width : 100%;
	height : 100%;
}

.main_text {
	width : 90vw;
	height : 5vw;
	float : left;
	position : relative;
}

.main_text h1 {
	width : 100%;
	text-align : center;
	position : absolute;
	top : 25%;
	font-family: "Dokdo", system-ui;
    font-weight: 400;
    font-style: normal;
	font-size : 3vw;
}

.section {
	width : 100vw;
	height : 37vw;
	border-bottom : 1px solid black;
	// position : relative;
}

.section .nav {
	width : 10%;
	height : 37vw;
	float : left;
	border-right : 1px solid black;
	font-weight: bold;
}

.section .nav a {
	display : block;
	text-align : center;
	padding : 10% 0;
	border-bottom : 1px solid black;
}

.section .main_contents {
	// position: relative;
	width : 80%;
	height : 95%;
	// height : 37vw;
	margin-top : 1%;
	margin-left : 5%;
	float : left;
}

.footer {
	height : 5vw;
	width : 100%;
	border-bottom : 1px solid black;
	font-weight : bold;
	font-size: 1vw;
}

.footer p{
	float : left;
	padding : 2vw 0 0 28vw;
}

.footer a {
	float : left;
	padding : 2vw 0 0 28vw;
}

.tags {
	width : 100%;
	height : 80%;
}

.tags a{
	display : block;
	font-size: 1.5vw;
	padding-top: 3.5vw;
}

.pagination {
	width : 100%;
	height: auto;
	text-align: center;
	top : 25%;
}
